*{
  box-sizing: border-box;
  margin: 0px;
}
body {
  display: flex;
  padding-bottom: 50px;
  justify-content: center;
  background-color: rgb(241, 241, 241);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
ul{
  padding: 0px;
}
.main-wrap{
position: relative;
display: flex;
min-width: 320px;
flex-direction: column;
justify-content: stretch;
align-items: stretch;
box-shadow:  0 0 25px rgb(207, 206, 206);
width: 45vw;
}
.main-heading{
  color: brown;
  opacity: 0.3;
  font-size: 8rem;
  text-align: center;
  margin: 0px;
}
.add-todo-field{
  height: 80px;
  border: 1px solid lightgrey;
  border-radius: 2px;
  color: lightgrey;
  padding-left: 75px;
  font-size: 1.5rem;
  font-style: italic;
}
.dropdown{
  position: absolute;
  visibility: hidden;
  background: url('dropdown.png') center no-repeat;
  background-size: contain;
  opacity: 0.5;
  top:18px;
  left:25px;
  width:40px;
  height: 40px; 
}
.dropdown:hover{
  opacity: 1;
}
.add-todo-field:focus{
  outline: none;
  color: black;
  font-style: normal;
}
.todo-checkbox{
  position: absolute;
  opacity: 0;
  z-index: 1;
  width: 50px;
  height: 50px;
}
.todo-text::before{
  content: url('checkbox.png');
  position: relative;
  top:10px;
  left: -10px;
}
.todo-text.checkedelem::before{
  content: url('checkbox-checked.png');
  position: relative;
  top: 10px;
  left: -10px;
}
.todo-text.checkedelem{
  text-decoration: line-through;
  color: rgb(211, 211, 211);
}
.todo-text input{
  font: inherit;
  color: inherit;
  border: none;
  height: 60px;
  flex-grow: 1;
  padding-left: 5px;
}
.todo-wrap, .form-footer{
  border: 1px solid lightgrey;
  border-radius: 2px;
  background-color: white;
  color:darkgray;
  font-size: 1.5rem;
}
.todo-wrap{
  display: flex;
  align-items: center;
}
.form-footer{
  height: 50px;
  padding: 5px;
}
.todo-text{
  display: flex;
  align-content: stretch;
  justify-content: stretch;
  flex-grow: 1;
  font-size: 1.5rem;
  color: black;
  margin-left: 25px;
  padding: 0px;
 
}
.form-footer{
  display: flex;
  justify-content: space-between;
}
.todos-count, .todos-completed{
  display: flex;
  align-items: center;
  padding: 5px;
  width: 15vw;
  font-size: 1rem;
  color: rgb(109, 103, 103);
}
.todos-filters{
  width: 15vw;
  min-width: 200px;
}
.todos-filters button, .todos-completed button{
  font-size: 1rem;
  color: rgb(109, 103, 103);
  border: none;
  background-color: white;
}
.todos-completed {
  justify-content: flex-end;
  visibility: hidden;
}
.todos-filters button:focus, .todos-completed button:focus{
  outline: none;
}
.decoration-line,.decoration-line2{
  height: 6px;
  background-color: rgb(247, 245, 245);
  border: 1px solid rgb(211, 211, 211);
}
.decoration-line{
  width:44.5vw;
  min-width: 318px;
}
.decoration-line2{
  width:44vw;
  min-width: 315px;
}
.delete-todo{
  position: absolute;
  visibility: hidden;
  background: url(./delete.png) center center no-repeat;
  background-size: contain;
  right: 10px;
  width:20px;
  height:20px;
  z-index: 0;
}
.todo-wrap:hover .delete-todo{
  visibility: visible;
  opacity: 0.8;
}
.todo:focus{
  outline: none;
  border: 1px solid black;
}


@media (max-width: 576px) {
  .add-todo-field{
    font-size: 1rem;
  }
  .main-heading{
    font-size: 5rem;
  }
  .todo-wrap, .form-footer{
    font-size: 0.5rem;
  }
  .todos-filters button, .todos-completed button{
    font-size: 0.5rem;
  }
  .todos-count, .todos-completed{
    font-size: 0.5rem;
  }
}

